<template>
<example-get-news :my-list="list">
<template #teamName>
  <p class="text-sm text-right font-semibold my-1 text-slate-900 my-2">اخبار لالیگا اسپانیا</p>
</template>
</example-get-news>

</template>

<script>
import ExampleGetNews from "@/components/main/ExampleGetNews";
import {onMounted, reactive} from "vue";
import EndPoints from "@/service/EndPoints";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "NewsSpanish",
  components: {ExampleGetNews},
 setup(){
   const list=reactive([])
   const total=onMounted(()=>{
     EndPoints.getGoalSpain()
         .then((response)=>{
           if(response.data.success){
             list.push(response.data.data.news)
           }
         })
         .catch((error)=>{
           handleError(error)
         })

   })
    return{
  list,total
    }
 }
}
</script>

<style scoped>

