<template>
<example-get-spain :my-list="box">

</example-get-spain>
</template>

<script>
import{reactive,onMounted} from 'vue'
import EndPoints from "@/service/EndPoints";
import ExampleGetSpain from "@/components/main/ExampleGetSpain";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "TableAwayspain",
  components: {ExampleGetSpain},
  setup(){
    const box=reactive([])
    const sum=onMounted(()=>{
      EndPoints.getGoalSpain()
      .then((response)=>{
        if(response.data.success){
          box.push(response.data.data.standing.standings_away)
        }
      })
      .catch((error)=>{
       handleError(error)
      })
    })
    return{
      box,
      sum,
    }
  }
}
</script>

