<template>
<example-get-spain :myList="list">

</example-get-spain>
</template>

<script>
import ExampleGetSpain from "@/components/main/ExampleGetSpain";
import{reactive,onMounted} from "vue";
import EndPoints from "@/service/EndPoints";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "TableAllspain",
  components: {ExampleGetSpain},
  setup() {
    const list = reactive([])
    const result = onMounted(() => {
      EndPoints.getGoalSpain()
          .then((response) => {
            if (response.data.success) {
              list.push(response.data.data.standing.standings)
            }
          })
          .catch((error) => {
          handleError(error)
          })
    })
    return {
   list,result,
    }
  }
}
</script>

