<template>
 <example-get-spain :my-list="list">

 </example-get-spain>
</template>

<script>
import{reactive,onMounted} from "vue";
import EndPoints from "@/service/EndPoints";
import ExampleGetSpain from "@/components/main/ExampleGetSpain";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "TableHomespain",
  components: {ExampleGetSpain},
  setup(){
    const list=reactive([])
    const total=onMounted(()=>{
      EndPoints.getGoalSpain()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data.standing.standings_home)
        }
      })
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
      list,
      total
    }
  }
}
</script>

