<template>
<section class="center-get-spain">
  <div class="container mx-auto p-2">
    <div class="w-full h-auto">
      <div v-for="items in myList" :key="items.id">
        <div v-for="value in items" :key="value.id">
         <div v-for="(myVal,index) in value.standings" :key="myVal.id">
           <table class="table w-full">
             <tr class="flex flex-row justify-between my-4 w-full">
               <td class="flex flex-row w-4/12">
                 <span class="text-black">{{index+=1}}</span>
                 <img @error="notShow" :src="`${myVal.team.logo}`" alt="" class="rounded-full w-9 h-9 mx-1">
                 <span class="text-right font-semibold text-slate-800 mx-1">{{myVal.team.name_fa}}</span>
               </td>
               <td class="flex flex-row w-1/12"><span class="text-sm text-slate-800">{{myVal.match_played}}</span></td>
               <td class="flex flex-row w-1/12"><span class="text-sm text-slate-800">{{myVal.win}}</span></td>
               <td class="flex flex-row w-1/12"><span class="text-sm text-slate-800">{{myVal.draw}}</span></td>
               <td class="flex flex-row w-1/12"><span class="text-sm text-slate-800">{{myVal.lose}}</span></td>
               <td class="flex flex-row w-1/12"><span class="text-sm text-slate-800">{{myVal.goal_scored - myVal.goal_against}}</span></td>
               <td class="flex flex-row w-1/12">
                 <span class="text-sm text-slate-800">{{myVal.goal_against}}</span>
                 <b>-</b>
                 <span class="text-sm text-slate-800">{{myVal.goal_scored}}</span>
               </td>
               <td class="flex flex-row w-1/12">
                 <span class="text-sm text-slate-800">{{myVal.overall_point}}</span>
               </td>
             </tr>
           </table>
         </div>
        </div>
      </div>
    </div>
  </div>
</section>
</template>

<script>
import{ref} from 'vue'
export default {
  name: "ExampleGetSpain",
 props:{
    myList:{
      type:Array,
      required:true
    }
 },
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_player_default.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
      notFound,
      notShow
    }
  }
}
</script>

