        <div @click="showNums(2)"><button @click="tabs='BestAssistspain'" type="button" :class="{bgBlack : count===2 , bgWhite: count===1}" class="text-sm font-semibold text-center rounded-sm text-white px-3 py-1 outline-none">پاس گل</button></div>
        <div @click="showNums(3)"><button @click="tabs='TotalSpanish'" type="button" :class="{bgBlack: count===3 , bgWhite: count===2}" class="text-sm font-semibold text-center rounded-sm text-white px-3 py-1 outline-none">مجموع</button></div>
